
.container__contacts{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 132px;
    padding-bottom: 165px;
    background-image:linear-gradient(to bottom,#ffffff 13%,#fff5f6 10%);
background-size: 100%;
background-repeat: no-repeat;
@media screen and(max-width:771px) {
    background-image: linear-gradient(to bottom, #ffffff 5%, #fff5f6 5%);
}

    @media screen and (min-width:1280px) {
        background-image:url(../images/contacts/contacts-2x.png),
        linear-gradient(to bottom,#ffffff 13%,#fff5f6 10%);
background-size: 100%;
background-repeat: no-repeat;
    }
}


.contacts__list{
    @media screen and (min-width:772px) {
        display: flex;
    }
    

}

.contacts__item{
    width: 330px;
    background: #FFFFFF;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    margin-right: 30px;
    &:last-child{
        margin-right: 0px;
    }
@media screen and(max-width:771px) {
    margin-right: 0px;
    margin-bottom: 20px;
    &:last-child{
        margin-bottom: 0px;
    }
}

    @media screen and (max-width:1279px) {
      width: 220px;
      
    }
}

.contacts__column{
    @media screen and (max-width:771px) {
        padding: 43px 21px 43px 20px;
        padding-left: 20px;
    }
    @media screen and (max-width:1279px) {
        padding: 50px 31px 50px 47px;

    }
    @media screen and (min-width:1280px) {
        padding: 52px 44px 62px 43px;
    }
}

.contacts__title{
font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 186%;
letter-spacing: 0.04em;
color: #FFFFFF;
width: 73px;
background: #F0D1A5;
border-radius: 6px;
display: flex;
justify-content: center;

}

.contacts__title-green{
    background: #C2E297;
    width: 138px;
}

.contacts__pre-title{
font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 186%;
letter-spacing: 0.04em;
color: #000000;
margin-top: 32px;
@media screen and (min-width:772px) and (max-width:1279px) {
    font-size: 14px;
 }
 @media screen and (max-width:771px) {
     font-size: 12px;
 }
}

.contacts__text{
    font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 183%;
letter-spacing: 0.04em;
color: #907E82;
&::after{
    content: "";
    display: block;
    border-bottom: solid 1px #E1E1E1;
    margin-top: 32px;
    margin-bottom: 32px;
    @media screen and (min-width:772px) and (max-width:1279px) {
        margin-top: 30px;
    margin-bottom: 30x;
     }
     @media screen and (max-width:771px) {
        margin-top: 25px;
        margin-bottom: 25px;
     }
}
@media screen and (min-width:772px) and (max-width:1279px) {
    font-size: 14px;
 }
 @media screen and (max-width:771px) {
     font-size: 12px;
 }
}

.working-days__list::after{
    content: "";
    display: block;
    border-bottom: solid 1px #E1E1E1;
    margin-top: 32px;
    margin-bottom: 32px;
}

.working-days__title{
font-family: DM Sans;
font-style: normal;
