 }
}

.working-days__pre-title{
font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 186%;
color: #FFA5BA;
@media screen and (min-width:772px) and (max-width:1279px) {
    font-size: 14px;
 }
 @media screen and (max-width:771px) {
     font-size: 12px;
 }
}

.working-days__pre-title-ind{
    margin-bottom: 15px; ;
}

.contacts__tel{
font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 183%;
color: #907E82;
text-decoration: none;
@media screen and (min-width:772px) and (max-width:1279px) {
    font-size: 14px;
 }
 @media screen and (max-width:771px) {
