 }
}

.contacts__mail{
font-family: DM Sans;
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 183%;
color: #907E82;
text-decoration: none;
@media screen and (min-width:772px) and (max-width:1279px) {
