.button__list{
    justify-content: center;
    @media screen and (max-width:771px) {
        margin-top: 54px;
    }
    @media screen and (min-width:772px) {
        display: flex;
    }
    @media screen and (min-width:772px) and (max-width:1279px) {
        margin-top: 81px;

    }
    @media screen and (min-width:1280px) {
        margin-top: 86px;
 }}

.button__item{
font-family: DM Sans;
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 21px;
display: flex;
align-items: center;
text-align: center;
}

.button-red{
font-family: DM Sans;
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 21px;
color: #FFFFFF;
width: 198px;
