.products__label {
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 1.16;
    text-align: center;
    text-transform: uppercase;
    color: #FFA5BA;

    @media screen and (min-width: 768px) {
        font-size: 26px;
        line-height: 1.15;
    }
}

.products__title {
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    margin-bottom: 144px;
    font-size: 34px;
    line-height: 1.14;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;

    @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 1.14;
        margin-bottom: 150px;
    }

    @media screen and (min-width: 1280px) {
        margin-bottom: 259px;
    }
}

.products__name {
    display: block;
    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ffffff;

    @media screen and (min-width: 1280px) {
        font-size: 30px;
        line-height: 1.13;
    }

    &::after {
        content: "";
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 30px;
        margin-bottom: 30px;
        width: 26px;
        height: 6px;
        background-image: url("../images/products/dots.png");
        background-size: 100%;
        background-color: inherit;
        background-repeat: no-repeat;
    }
}

.products__slider {
    width: 200px;
    height: 124px;

    @media screen and (min-width: 768px) {
        width: 198px;
    }  
    
    @media screen and (min-width: 1280px) {
        width: 241px;
        height: 106px;
    }    
}

.products__text {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 152%;
    text-align: center;
    letter-spacing: 0.04em;
    color: #ffffff;

    @media screen and (min-width: 768px) {
        line-height: 176%;
        text-align: center;
    }
    
    @media screen and (min-width: 1280px) {
        font-size: 16px;
        line-height: 176%;
    } 
}

.products__button {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    padding-left: 5px;
    border-radius: 50%;
    border-color: transparent;
    background-color: #ffffff;
    fill: #D41443;
    width: 40px;
    height: 40px;

    &:hover,
    &:focus {
        background-color: #D41443;
        fill: #ffffff;
    }
    
    @media screen and (min-width: 1280px) {
        margin-top: 62px;
    }
}

.products__icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

